#about {
    background-image: linear-gradient(to right bottom, #37084d, #250d41, #1a103c, #17133f, #0e1339, #0b1336, #091233, #071130, #071130, #071130, #071130, #071130);
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    color: #fff;
    text-align: center;
}

.blink {
    animation: blink .875s 1s infinite;
    border-right: 3px solid #fff;
    padding-right: 0.5rem;
}

.about__sticky {
    width: 40%;
}

.code-nav {
    display: flex;
    margin-bottom: 2rem;
}

.red-circle,
.green-circle,
.orange-circle {
    background-color: rgb(255, 21, 0);
    border-radius: 100%;
    width: 0.75rem;
    height: 0.75rem;
    margin-right: 0.5rem;
}

.orange-circle {
    background-color: rgb(255 159 36);
}

.green-circle {
    background-color: rgb(0, 255, 85);
}

.about__sticky .code__section {
    width: 90%;
    margin-top: 17.5%;
    margin-bottom: 5%;
    padding: 4% 10% 10% 5%;
    text-align: left;
    font-size: 1.25rem;
    border-radius: 0.6rem;
    border: 1px solid rgb(47, 50, 83);
    /*  border-image: linear-gradient(to right, #d80b77, #11152c) 1 0 0 0;*/
    background-image: linear-gradient(to left top, #071130, #071130, #071130, #071130, #071130, #0a1131, #0c1031, #0f1032, #150f33, #1a0d33, #200b33, #250832);
}

.about__sticky span,
.about__sticky code,
.about__sticky .code__section {
    font-family: Consolas;
}

.about__sticky .code-margin {
    margin-left: 2.5rem;
}

.about__sticky .code-color-gray {
    color: rgb(165, 165, 165);
}

.about__sticky .code-color-cyan {
    color: cyan;
}

.about__sticky .code-color-purple {
    color: rgb(160, 0, 160);
    font-weight: bold;
}

.about__sticky .code-color-yellow {
    color: yellow;
}

.about__sticky .code-color-orange {
    color: rgb(255, 159, 36);
}

.about__content {
    margin-top: 7rem;
    height: 75%;
    width: 37.5%;
    text-align: justify;
    word-spacing: 4px;
    letter-spacing: 0.3px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-self: flex-start;
}

.about__content p {
    font-size: 1.4rem;
    font-family: 'Chakra Petch', sans-serif;
}

@keyframes blink {
    0% {
        border-right-color: transparent;
    }

    100% {
        border-right-color: #fff;
    }
}

@media screen and (max-width: 1650px) {
    .about__sticky {
        width: 42.5%;
    }

    .about__sticky .code__section {
        margin-top: 25%;
        padding-top: 5%;
    }
}

@media screen and (max-width: 1600px) {
    .about__content {
        margin-top: 6rem;
        height: 80%;
    }

    .about__content p {
        font-size: 1.3rem;
    }
}

@media screen and (max-width: 1550px) {
    .about__sticky .code__section {
        padding-right: 2.5%;
    }
}

@media screen and (max-width: 1480px) {
    .about__sticky .code__section {
        padding-right: 5%;
        width: 95%;
    }

    .about__content {
        width: 40%;
    }
}

@media screen and (max-width: 1380px) {
    .about__sticky .code__section {
        padding-right: 4%;
        width: 97.5%;
    }
}

@media screen and (max-width: 1350px) {
    #about {
        justify-content: space-around;
    }

    .about__sticky .code__section {
        width: 100%;
    }

    .about__content {
        width: 42.5%;
        margin-right: 1rem;
    }
}

@media screen and (max-width: 1100px) {
    #about {
        justify-content: space-between;
        padding: 0 4.5%;
    }

    .about__sticky {
        width: 47.5%;
    }

    .about__sticky .code__section {
        margin-top: 42.5%;
        padding-top: 7.5%;
        padding-bottom: 14%;
    }

    .about__content {
        width: 47.5%;
        margin-right: 0;
        margin-top: 5rem;
        height: 82.5%;
    }
}

@media screen and (max-width:910px) {
    #about {
        flex-direction: column;
        align-items: center;
        padding-bottom: 5.5%;
    }

    .about__sticky {
        width: 75%;
    }

    .about__sticky .code__section {
        border-width: 2px;
        background-image: linear-gradient(to right, #11152c, #0f132f, #0d1232, #0b0f35, #0a0d38);
        margin-top: 5%;
        padding-right: 10%;
        padding-left: 7.5%;
        padding-bottom: 7.5%;
    }

    .about__content {
        width: 85%;
        margin: 0 auto;
        height: 100%;
    }

    .about__content p {
        font-size: 1.2rem;
    }
}


/* iPhone 14 Pro Max */

@media screen and (max-width:430px) {
    #about {
        padding: 0 1%;
        height: 125vh;
    }

    .about__sticky {
        width: 97.5%;
    }

    .about__sticky .code__section {
        margin-top: 2.5%;
        margin-bottom: 2.5%;
        padding: 5%;
        font-size: 1.15rem;
    }

    .about__sticky span,
    .about__sticky code,
    .about__sticky .code__section {
        font-family: 'Chakra Petch', sans-serif;
    }

    .code-nav {
        margin-bottom: 1rem;
    }

    .about__sticky .code-margin {
        margin-left: 2rem;
    }

    .about__content {
        width: 92.5%;
        word-spacing: 1px;
        letter-spacing: 0.1px;
        text-align: left;
    }

    .about__content p {
        font-size: 1rem;
    }
}