#technologies {
    overflow: hidden;
    height: 60vh;
    position: relative;
    background-image: linear-gradient(to right, #370d4a, #32124e, #2b1751, #241b54, #1c1e56, #1d2263, #1e2570, #1f297d, #372c97, #532aaf, #7322c5, #9600d8);
    display: grid;
    grid-gap: 1px;
    grid-template-columns: repeat(8, 12.5%);
    grid-template-rows: repeat(4, 25%);
    animation: gradient 7s linear infinite;
    background-size: 200%;
}

.TECHNOSECTION {
    margin: 0 12.5%;
    width: 75%;
    pointer-events: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    position: absolute;
    z-index: 50;
    top: 25%;
    transform: translateY(-20%);
}

.single__technologie {
    margin: 3rem 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc(100%/6);
}

.single__technologie img {
    object-fit: contain;
    object-position: center center;
    height: 90px;
    width: 90px;
    margin-bottom: 1.25rem;
    filter: grayscale(0);
    user-select: none;
    background-color: rgb(15, 15, 15);
}

.single__technologie p {
    color: #fff;
    font-size: 1.5rem;
    font-family: "Source Sans Pro", sans-serif;
    font-family: 'Chakra Petch';
    background-color: rgb(15, 15, 15);
}

#technologies .square {
    overflow: hidden;
    user-select: none;
    transition: .125s ease-in-out;
    background-color: rgb(15, 15, 15);
}

@keyframes gradient {
    0% {
        background-position: 0% 0%;
    }

    50% {
        background-position: 100% 100%;
    }

    100% {
        background-position: 0% 0%;
    }
}

@media screen and (max-width: 1650px) {
    .TECHNOSECTION {
        width: 80%;
        margin: 0 10%;
    }
}

@media screen and (max-width: 1500px) {
    #technologies {
        grid-template-columns: repeat(6, 16.6666666667%);
        grid-template-rows: repeat(5, 20%);
    }

    .TECHNOSECTION {
        width: 87.5%;
        margin: 0 6.25%;
    }
}

@media screen and (max-width: 1400px) {
    .TECHNOSECTION {
        width: 90%;
        margin: 0 5%;
    }
}

@media screen and (max-width: 1300px) {
    #technologies {
        grid-template-columns: repeat(6, 16.6666666667%);
        grid-template-rows: repeat(4, 25%);
    }

    .TECHNOSECTION {
        width: 92.5%;
        margin: 0 3.75%;
    }

    .single__technologie img {
        width: 80px;
        height: 80px;
    }
}

@media screen and (max-width: 1200px) {
    .TECHNOSECTION {
        width: 95%;
        margin: 0 2.5%;
    }

    .single__technologie img {
        width: 75px;
        height: 75px;
    }

    .single__technologie p {
        font-size: 1.425rem;
    }
}

@media screen and (max-width:910px) {
    #technologies {
        height: 67.5vh;
        grid-template-columns: repeat(5, 20%);
        grid-template-rows: repeat(4, 25%);
    }

    .TECHNOSECTION {
        width: 100%;
        margin: 0 auto;
    }

    .single__technologie {
        width: calc(100%/3);
        margin: 1rem 0;
    }

    .single__technologie img {
        width: 67.5px;
        height: 67.5px;
        margin-bottom: 1rem;
    }

    .single__technologie p {
        font-size: 1.325rem;
    }
}

@media screen and (max-width:430px) {
    #technologies {
        height: 87.5vh;
        grid-template-columns: repeat(3, 33.333333%);
        grid-template-rows: repeat(5, 20%);
    }

    .TECHNOSECTION {
        top: 20.5%;
    }

    .single__technologie {
        width: calc(100%/2);
        margin: 0.75rem 0;
    }

    .single__technologie img {
        width: 55px;
        height: 55px;
    }

    .single__technologie p {
        font-size: 1.15rem;
    }
}