.phone {
    margin-bottom: 5rem;
    display: grid;
    place-content: center;
    padding: 12px;
    background-color: rgb(23 23 23);
    background-color: transparent;
    margin-left: 10%;
}

.floating__phone {
    background-color: rgb(104, 31, 171);
    border-radius: 24px;
}

.floating__phone-motion {
    position: relative;
    height: 24rem;
    width: 14rem;
    border-width: 2px;
    border-bottom-width: 4px;
    border-right-width: 4px;
    border-color: rgb(255 255 255);
    border-left-color: rgb(229 229 229);
    border-top-color: rgb(229 229 229);
    border-radius: 24px;
    background-color: rgb(23 23 23);
    padding: 0.25rem;
    padding-left: 3px;
    padding-top: 3px;
}

.header__bar {
    /* -translate-x-[50%]  */
    position: absolute;
    left: 50%;
    top: 0.625rem;
    z-index: 10;
    height: 0.7rem;
    width: 3.5rem;
    border-radius: 0.375rem;
    background-color: rgb(0, 0, 0);
    transform: translateX(-50%);
}

.icons__container {
    position: absolute;
    right: 0.75rem;
    top: 0.5rem;
    z-index: 10;
    display: flex;
    gap: 0.5rem;
}

.wifi__icon,
.battery__icon {
    font-size: 1.1rem;
    color: #000;
}

.screen {
    position: relative;
    z-index: 0;
    display: grid;
    height: 100%;
    width: 100%;
    place-content: center;
    overflow: hidden;
    border-radius: 20px;
    background-color: white;
}

.screen img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    user-select: none;
}

.screen button {
    user-select: none;
    cursor: pointer;
    position: absolute;
    bottom: 1rem;
    left: 1rem;
    right: 1rem;
    border-radius: 0.5rem;
    border-width: 2px;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-size: 1.1rem;
    font-weight: bold;
    line-height: 1.25rem;
    z-index: 10;
    color: #000;
    backdrop-filter: blur(8px);
    background-color: #fff;
}

.screen__background {
    /*  PRZETESTOWAĆ TO  */
    /* className="-left-32 -top-32 h-64 w-64"*/
    position: absolute;
    left: 50%;
    bottom: -18rem;
    height: 24rem;
    width: 24rem;
    transform: translateX(-50%);
    background-color: #06194f;
    background-color: rgb(104, 31, 171);
    border-radius: 100%;
    z-index: -1;
}

@keyframes fadeGrid {
    from {
        background-position: 0% center;
    }

    to {
        background-position: -200% center;
    }
}


