/*  PROJECTS SECTION  */

.projects__heading {
    user-select: none;
    text-align: center;
    width: 50%;
    font-family: 'Cousine', monospace;
    color: #fff;
    font-size: 3.75rem;
    font-weight: bold;
    margin: 7.5vh auto 2.5vh;
    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
    animation: glitch 700ms linear 5s infinite alternate;
}

.projects__heading::before {
    content: "";
    background-color: cyan;
    display: inline-block;
    height: 4px;
    position: relative;
    vertical-align: middle;
    width: 35%;
    margin-right: 1.75rem;
}

.projects__heading span {
    font-family: 'Chakra Petch', sans-serif;
    color: #ae00ff;
    font-size: 5rem;
    margin-left: 4px;
}

.projects__container {
    margin: 7.5vh auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 80%;
}

.project__box {
    width: 37.5%;
}

.project__description {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-top: 1.1rem;
    margin-bottom: 7.5vh;
}

.project__description p {
    margin-left: 0.25rem;
    text-transform: uppercase;
    font-size: 1.4rem;
    letter-spacing: 0.1px;
    text-align: center;
    font-family: 'Cousine', monospace;
    color: #ae00ff;
    user-select: none;
    text-align: left;
}

.project__description .row {
    background-color: cyan;
    height: 2px;
    position: relative;
    width: 45%;
    overflow: hidden;
}

.project__description .icon svg {
    font-size: 1.5rem;
    color: #fff;
    display: flex;
}

.project__card {
    width: 100%;
    height: 350px;
    border-radius: 12.5px;
    background-color: #303030;
    position: relative;
    overflow: hidden;
}

.project__card img {
    cursor: pointer;
    user-select: none;
    position: absolute;
    overflow: hidden;
    object-position: top;
    /* LUB center */
    object-fit: cover;
    bottom: 0;
    right: 50%;
    width: 100%;
    height: 100%;
    transform: translate(50%, 0%) rotate(0);
    transition: .3s cubic-bezier(0.39, 0.575, 0.565, 1);
    filter: grayscale(67.5%) blur(1.3px);
}

.project__card:hover img {
    transform: translate(50%, 0%) rotate(0deg) scale(1.1);
    filter: none;
}

#projects {
    height: auto;
    display: flex;
    flex-direction: column;
    background-color: #0e0e0e;
    padding-bottom: 7.5vh;
    background-image: linear-gradient(to left bottom, #091233, #0e112c, #111026, #120f1f, #110f19, #100f16, #100f13, #0f0f0f, #0f0f0f, #0f0f0f, #0e0e0e, #0e0e0e);
}

@media screen and (max-width: 1650px) {
    .projects__container {
        width: 87.5%;
    }

    .project__box {
        width: 40%;
    }
}

@media screen and (max-width: 1500px) {
    .projects__container {
        width: 90%;
    }

    .project__box {
        width: 40%;
    }
}

@media screen and (max-width: 1350px) {
    .projects__container {
        width: 97.5%;
    }

    .project__box {
        width: 40%;
    }

    .project__description p {
        font-size: 1.475rem;
    }
}

@media screen and (max-width: 1300px) {
    .project__box {
        width: 42.5%;
    }

    .project__description p {
        font-size: 1.5rem;
    }
}

@media screen and (max-width: 1140px) {
    .projects__heading {
        width: 75%;
    }

    .projects__container {
        width: 100%;
    }

    .project__box {
        width: 45%;
    }
}

@media screen and (max-width: 1010px) {
    .projects__heading {
        width: 95%;
        margin-top: 5vh;
        margin-bottom: 1vh;
    }

    .projects__heading::before {
        margin-right: 2.75rem;
        height: 3px;
    }

    .projects__container {
        flex-direction: column;
        margin-top: 6vh;
        margin-bottom: 0;
    }

    .project__box {
        width: 65%;
    }

    .project__card {
        height: 375px;
    }

    .project__description {
        margin-top: 0.25rem;
        justify-content: center;
        flex-direction: column-reverse;
    }

    .project__description p {
        font-size: 1.825rem;
        margin-left: 0;
        letter-spacing: 1px;
        font-family: 'Chakra Petch', sans-serif;
    }

    .project__description .row {
        display: none;
    }

    .project__description .icon svg {
        display: none;
    }
}

@media screen and (max-width:430px) {
    .project__box {
        width: 95%;
    }

    .project__card {
        height: 250px;
    }

    .project__description p {
        font-size: 1.4rem;
    }

    .project__description {
        margin-bottom: 4vh;
    }
}