/* further contact section code */

#contact {
    display: flex;
    flex-direction: column;
}

.contact__container {
    display: flex;
    justify-content: center;
    margin-top: auto;
}

.contact__infos {
    text-transform: uppercase;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-bottom: 5rem;
    margin-right: 2.5rem;
}

.contact__infos p,
.contact__infos a {
    color: #fff !important;
    font-weight: bold;
    font-size: 3.25rem;
    margin: 2rem 0;
    font-family: 'Roboto', sans-serif;
}

.contact__number {
    align-self: flex-end;
}

.contact__mail {
    display: flex;
    align-items: center;
}

.contact__mail .mail__icon {
    margin-right: 1rem;
}

.lets__work {
    margin-top: 2.5rem;
    transform: translateX(-12.5%);
}


/* FOOTER */

footer {
    overflow: hidden;
    color: #fff;
    width: 87.5%;
    margin: auto auto 0;
}

footer a {
    color: #fff;
}

.footer__container {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 0 auto 2vh;
}

.footer__city {
    font-family: 'Chakra Petch';
    font-size: 1.25rem;
}

.hub__lep {
    font-family: 'Lato', sans-serif;
    font-size: 1.45rem;
    user-select: none;
    font-weight: 700;
}

.name__animation {
    animation: glitch 300ms linear;
}

.hub-lep:hover {
    color: transparent;
}

.footer__link {
    user-select: none;
    font-family: 'Cousine', monospace;
    cursor: pointer;
    display: block;
    font-size: 10.5px;
    transition: all .25s cubic-bezier(.175, .885, .32, 1.275);
}

.footer__link span {
    font-size: 14px;
    margin-left: 1rem;
    font-weight: bold;
    font-family: 'Chakra Petch';
}

.footer__link:hover {
    transform: rotateY(12deg) translateZ(0);
}

@media screen and (max-width: 1460px) {

    .contact__infos p,
    .contact__infos a {
        font-size: 2.9rem;
    }
}

@media screen and (max-width: 1330px) {
    .contact__infos {
        margin-right: 1.5rem;
    }

    .contact__infos p,
    .contact__infos a {
        font-size: 2.575rem;
    }
}

@media screen and (max-width:1200px) {
    .contact__infos {
        margin-right: 0rem;
    }

    .contact__infos p,
    .contact__infos a {
        font-size: 2.4rem;
    }

    footer {
        width: 90%;
    }
}

@media screen and (max-width:1130px) {
    .lets__work {
        transform: translateX(0);
        align-self: center;
    }

    .contact__number {
        align-self: center;
    }

    .footer__container {
        margin-bottom: 1.5vh;
    }
}

@media screen and (max-width:1020px) {

    .contact__infos p,
    .contact__infos a {
        font-size: 2.25rem;
    }

    .phone {
        margin-left: 6%;
    }
}

@media screen and (max-width:950px) {
    footer {
        width: 92.5%;
    }
}

@media screen and (max-width:910px) {
    #contact {
        height: 87.5vh;
    }

    .phone {
        margin-bottom: 2rem;
    }

    .contact__container {
        flex-direction: column-reverse;
    }

    .contact__infos {
        margin-bottom: 0;
    }

    .contact__infos p,
    .contact__infos a {
        margin: 1rem 0;
    }

    .contact__mail {
        align-self: center;
    }

    footer {
        margin-top: 5vh;
        margin-bottom: 0.5vh;
    }

    .hub__lep {
        display: none;
    }
}

@media screen and (max-width:430px) {

    .contact__infos p,
    .contact__infos a {
        margin: 0.35rem 0;
        font-size: 1.4rem;
    }

    footer {
        width: 95%;
        margin-bottom: 0;
    }

    .footer__container {
        margin-bottom: 1.25vh;
    }
}